
import { defineComponent, ref, onMounted } from 'vue'
import ConvLayer from './ConvLayer.vue'
import FullLayer from './FullLayer.vue'
import { hypothesize } from './NeuralNetwork'
import ImageMeta from './ImageMeta'

export default defineComponent({
  components: {
    ConvLayer,
    FullLayer
  },

  props: {
    msg: String
  },

  setup(props) {
    const scaleCanvas = document.createElement('canvas')
    const scaleCtx = scaleCanvas.getContext('2d')!
    const numeral = ref<HTMLCanvasElement>()
    const filter = ref<number[][]>([])
    const filter2 = ref<number[][]>([])
    const full = ref<number[]>([])
    const full2 = ref<number[]>([])
    const prediction = ref<string>('')
    const subPrediction = ref<string>('')
    let timeoutId = -1
    let isDrawing = false
    let x = 0
    let y = 0

    const drawLine = (t: number, u: number) => {
      const context = numeral.value!.getContext('2d')!
      context.beginPath()
      context.strokeStyle = 'black'
      context.lineWidth = 7
      context.lineJoin = "round"
      context.lineCap = "round"
      context.moveTo(x, y)
      context.lineTo(t, u)
      context.stroke()
      context.closePath()
    }

    const convertToArray = (dat: Uint8ClampedArray) => {
      const arr: number[] = []
      for (let i = 3; i < dat.length; i+= 4) {
        arr.push(dat[i])
      }

      return arr
    }

    const hyp = () => {
      const context = numeral.value!.getContext('2d')!
      scaleCtx.clearRect(0, 0, 130, 130)

      // pull out full image data
      const originalData = convertToArray(context.getImageData(0, 0, 130, 130).data)

      // find top, bottom, left-most, right-most pixels
      // TODO: monad?
      const meta = new ImageMeta(130)
      for (let i = 0; i < originalData.length; i++) {
        if (originalData[i] > 0) {
          meta.trySetTop(i)
          meta.setBottom(i)
          meta.setLeft(i)
          meta.setRight(i)
        }
      }

      // calc bounding box
      const boundingBox = [meta.getWidth(), meta.getHeight()]

      // calc scale ratio that scales largest box dimension to 20px
      const scaleRatio = 20 / Math.max(boundingBox[0], boundingBox[1])

      // get bounding box image from canvas and paint on temp canvas
      const croppedData = context.getImageData(meta.left, meta.top, boundingBox[0], boundingBox[1])
      const tempCanvas = document.createElement('canvas')
      tempCanvas.getContext('2d')!.putImageData(croppedData, 0, 0)

      // set scale on scaleCanvas
      scaleCtx.scale(scaleRatio, scaleRatio)

      // redraw image at scale
      scaleCtx.drawImage(tempCanvas, 0, 0)

      // pull 20x20 image data from scaleCanvas
      const sizedData = scaleCtx.getImageData(0, 0, 20, 20)
      const sizedArray = convertToArray(sizedData.data)

      // calculate weighted centoid of image
      let xsum = 0
      let ysum = 0
      let weights = 0

      for (let i = 0; i < sizedArray.length; i++) {
        const px = sizedArray[i]
        if (px > 0) {
          xsum += px * ((i % 20) + 1)
          ysum += px * (Math.floor(i / 20) + 1)
          weights += px
        }
      }

      const centoid = [xsum / weights, ysum / weights]

      // clear scale canvas and reset scale
      scaleCtx.setTransform(1, 0, 0, 1, 0, 0)
      scaleCtx.clearRect(0, 0, 20, 20)

      // calc difference between centoid coords and 28x28 centre coords
      // and paint the 20x20 on scale canvas centred to 28x28
      scaleCtx.putImageData(sizedData, Math.round(14.5 - centoid[0]), Math.round(14.5 - centoid[1]))

      // extract 28x28 image from scaleCanvas
      const finalImageData = convertToArray(scaleCtx.getImageData(0, 0, 28, 28).data)

      // get max value
      const max = finalImageData.reduce((a, c) => {
        if (a > c) return a
        else return c
      })

      // normalise using max value and hypothesize
      const guess = hypothesize(finalImageData.map(x => x / max))
      prediction.value = ''+guess[0][0]
      subPrediction.value = ''+guess[0][1]
      filter.value = guess[1][0]
      filter2.value = guess[1][1]
      full.value = guess[2][0]
      full2.value = guess[2][1]
    }

    const startDraw = (event: any) => {
      if (timeoutId > -1) window.clearTimeout(timeoutId)
      isDrawing = true
      x = event.offsetX
      y = event.offsetY
    }

    const paint = (event: any) => {
      if (isDrawing === true) {
        drawLine(event.offsetX, event.offsetY)
        x = event.offsetX
        y = event.offsetY
      }
    }

    const stopDraw = (event: any) => {
      if (isDrawing == true) {
        drawLine(event.offsetX, event.offsetY)
        isDrawing = false
        x = 0
        y = 0
        timeoutId = window.setTimeout(hyp, 500)
      }
    }

    const clear = (event: any) => {
      numeral.value!.getContext('2d')!.clearRect(0, 0, 130, 130)
      scaleCtx.clearRect(0, 0, 130, 130)
    }

    return {
      numeral,
      startDraw,
      paint,
      stopDraw,
      clear,
      hyp,
      filter,
      filter2,
      full,
      full2,
      prediction,
      subPrediction
    }

  }
})
