import thetas from '../assets/thetas.json'

function sigmoid(x: number): number {
  return (1 / (1 + (Math.E ** -x)))
}

function toGrid(input: number[], width: number): number[][] {
  const fst: number[] = new Array(29)
  const grid: number[][] = [fst.fill(0, 0)]

  for (let j = 0; j < input.length; j+= width) {
    const slc = input.slice(j, j+width)
    slc.push(0)
    grid.push(slc)
  }
  return grid
}

function outputGrid(size: number, bias: number): number[][] {
  const result: number[][] = []
  for (let x = 0; x < size; x ++) {
    const row = []
    for (let y = 0; y < size; y ++) {
      row.push(bias)
    }
    result.push(row)
  }
  return result;
}

function convolutionalForwardProp(input: number[][], output: number[][], filter: number[]): number[][] {
  const bound = input[0].length - 4

  for (let r = 0; r < bound; r+=2) {
    for (let c = 0; c < bound; c+=2) {
      let wndw: number[] = []
      wndw = wndw.concat(input[r].slice(c, c+5))
      wndw = wndw.concat(input[r+1].slice(c, c+5))
      wndw = wndw.concat(input[r+2].slice(c, c+5))
      wndw = wndw.concat(input[r+3].slice(c, c+5))
      wndw = wndw.concat(input[r+4].slice(c, c+5))

      for (let x = 0; x < wndw.length; x++) {
        output[r/2][c/2] += filter[x] * wndw[x]
      }
    }
  }

  return output
}

function convFilter(input: number[][], filter: number[]): number[][] {
  const size = input[0].length
  const channels = input.length / size
  let output: number[][] = outputGrid((size - 3)/2, filter[0])

  for (let c = 0; c < channels; c++) {
    const offset = c*size
    const toffset = c*25 + 1
    output = convolutionalForwardProp(
      input.slice(offset, offset+size),
      output,
      filter.slice(toffset, toffset+25)
    )
  }

  return output.map(r => r.map(x => sigmoid(x)))
}

function fullyConnected(input: number[], thetas: number[][]): number[] {
  return thetas.map(row => {
    let res = row[0]
    for (let i = 0; i < input.length; i++) {
      res += row[i+1] * input[i]
    }
    return res
  }).map(x => sigmoid(x))
}

export function hypothesize(input: number[]): [[number, number], number[][][], number[][]] {
  const grid: number[][] = toGrid(input, 28)

  const lyr1: number[][][] = thetas[0].map(filter => convFilter(grid, filter))
  const lyr2: number[][][] = thetas[1].map(filter => convFilter(lyr1.flat(), filter))
  const lyr3: number[] = fullyConnected(lyr2.flat().flat(), thetas[2])
  const lyr4: number[] = fullyConnected(lyr3, thetas[3])
  // console.log(lyr4)
  let digit = -1
  let max = 0
  for (let i = 0; i < lyr4.length; i++) {
    if (lyr4[i] > max) {
      max = lyr4[i]
      digit = i
    }
  }

  let digit2 = -1
  max = 0
  for (let i = 0; i < lyr4.length; i++) {
    if (lyr4[i] > max && i != digit) {
      max = lyr4[i]
      digit2 = i
    }
  }

  const res: number[][][] = [lyr1, lyr2].map(x => x.map(y => y.flat()))
  return [[digit, digit2], res, [lyr3, lyr4]]
}
