
import { defineComponent, watch, ref } from 'vue'
import Channel from './Channel.vue'

export default defineComponent({
  components: {
    Channel
  },

  props: {
    data: Array,
    shifter: String,
    filterCount: Number
  },

  setup(props) {
    let pos = 0
    const posClass = ref('show-0')
    const isBig = props.filterCount! > 4

    watch(() => props.shifter, (dir, prev) => {
      if (dir![0] == 'r' && pos < props.filterCount!) pos++
      else if (dir![0] == 'l' && pos > 0) pos--
      posClass.value = 'show-' + pos
    })

    return {
      posClass,
      isBig
    }

  }
})
