<template>
  <div class="layer">
    <button @click="shiftLeft" class="shift left">&lt;</button>
    <div class="crop">
      <ul class="filters" :class="posClass">
        <li v-for="(n, idx) in data" :key="idx" :class="getClass(n)" v-bind:data-px="round(idx, n)"></li>
      </ul>
    </div>
    <button @click="shiftRight" class="shift right">&gt;</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from 'vue'

export default defineComponent({
  props: {
    data: Array,
    nodeCount: Number
  },

  setup(props) {
    let pos = 0
    const posClass = ref('show-0')

    const shiftLeft = (e: any) => {
      if (pos > 0) {
        pos-=5
        posClass.value = 'show-' + pos
      }
    }

    const shiftRight = (e: any) => {
      if (pos < props.nodeCount!) {
        pos+=5
        posClass.value = 'show-' + pos
      }
    }

    const getClass = (value: number) => {
      const interval = Math.floor(value*100)
      return "active-" + interval
    }

    const round = (idx: number, px: number) => "[" + idx + ", " + (px * 100).toFixed(2) + "]"

    return {
      shiftLeft,
      shiftRight,
      getClass,
      posClass,
      round
    }
  }
})

</script>

