export default class ImageMeta {
  private t: number;
  private b: number;
  private r: number;
  private l: number;

  constructor(readonly size: number) {
    this.t = -1;
    this.b = 0;
    this.r = 0;
    this.l = size + 1;
  }

  get top(): number {
    return this.t
  }

  get bottom(): number {
    return this.b
  }

  get left(): number {
    return this.l
  }

  get right(): number {
    return this.r
  }

  public trySetTop(px: number) {
    if (this.t < 0) this.t = Math.floor(px / this.size)
  }

  public setBottom(px: number) {
    this.b = Math.floor(px / this.size)
  }

  public setLeft(px: number) {
    const pos = px % this.size
    if (pos < this.l) this.l = pos
  }

  public setRight(px: number) {
    const pos = px % this.size
    if (pos > this.r) this.r = pos
  }

  public getHeight(): number {
    return this.b - this.t
  }

  public getWidth(): number {
    return this.r - this.l
  }

}
