<template>
  <div class="layer">
    <button @click="shiftLeft" class="shift left">&lt;</button>
    <div class="crop">
      <Filter ref="filter" :data="data" :shifter="shifter" :filter-count="filterCount"></Filter>
    </div>
    <button @click="shiftRight" class="shift right">&gt;</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from 'vue'
import Filter from './Filter.vue'

export default defineComponent({
  components: {
    Filter
  },

  props: {
    data: Array,
    filterCount: Number
  },

  setup() {

    const shifter = ref<string>('none');
    const shiftLeft = (e: any) => shifter.value = (shifter.value == 'l' ? 'l!' : 'l');
    const shiftRight = (e: any) => shifter.value = (shifter.value == 'r' ? 'r!' : 'r');

    return {
      shiftLeft,
      shiftRight,
      shifter
    }
  }
})

</script>

<style scoped lang="less">

@import '../assets/layer.less';

</style>
