<template>
  <ul>
    <li v-for="(px, idx) in data" :key="idx" :class="getClass(px)" v-bind:data-px="round(px)"></li>
  </ul>
</template>

<script lang="ts">
// import { Vue } from 'vue-class-component';
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    filter: Number,
    data: Array,
    isBig: Boolean,
  },

  setup(props) {
    const getClass = (value: number) => {
      const interval = Math.floor(value*100)
      if (props.isBig) {
        return "active-" + interval + " big"
      } else {
        return "active-" + interval
      }
    }

    const round = (px: number) => "[" + props.filter! + ", " + (px * 100).toFixed(2) + "]"

    return {
      getClass,
      round
    }

  }
})
</script>

