
import { defineComponent, ref, onMounted } from 'vue'

export default defineComponent({
  props: {
    data: Array,
    nodeCount: Number
  },

  setup(props) {
    let pos = 0
    const posClass = ref('show-0')

    const shiftLeft = (e: any) => {
      if (pos > 0) {
        pos-=5
        posClass.value = 'show-' + pos
      }
    }

    const shiftRight = (e: any) => {
      if (pos < props.nodeCount!) {
        pos+=5
        posClass.value = 'show-' + pos
      }
    }

    const getClass = (value: number) => {
      const interval = Math.floor(value*100)
      return "active-" + interval
    }

    const round = (idx: number, px: number) => "[" + idx + ", " + (px * 100).toFixed(2) + "]"

    return {
      shiftLeft,
      shiftRight,
      getClass,
      posClass,
      round
    }
  }
})

